.header {
  padding: 30px 0;
  background: $black-to-bottom;
  min-height: 120px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;

  @include vp-1023 {
    padding: 20px 0;
    position: relative;
    top: unset;
    left: unset;
    z-index: 1;
    min-height: 100px;
  }

  @include vp-767 {
    min-height: 70px;
    padding: 15px 0;
  }

  &__wrapper {
    max-width: 1160px;
    display: flex;
    justify-content: space-between;
  }

  &__logo {
    margin-right: 30px;

    a {
    display: flex;

      img:first-of-type {
        width: 55px;
        height: 55px;
        margin-right: 15px;

        @include vp-767 {
          width: 35px;
          height: 35px;
        }
      }

      img:last-of-type {
        width: 145px;
        height: 55px;

        @include vp-767 {
          display: none;
        }
      }
    }
  }

  &__menu {
    display: flex;
  }

  a {
    color: $color-alice-blue-mod;
  }

  &__feedback {
    margin: 5px 0 0 30px;
    align-self: flex-start;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    font-family: $ff-roboto;
    min-height: 40px;
    min-width: 160px;
    padding: 10px 15px;
    border: 1px solid rgba($color-alice-blue-mod, 0.4);
    border-radius: 3px;

    @include vp-1023 {
      display: none;
    }
  }
}
